import { ksbLog } from '@api';
import { bridge } from '@bridge';
import { CommonProps } from '@interface';
import React, { FC } from 'react';
import './index.less';

const CloseButton: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            className = '',
            text = '残忍关闭',
            size = 'nomal', // nomal | 正常大按钮; medium | 双btn; small | 小按钮
            logInfo: { itemClickType = '' } = {},
        },
        animationType = '', // animate-beat | 跳动；animate-beat | 抖动；animate-beat | 扫光
        style = {},
        info,
    } = props;

    return (
        <div
            data-itemClickType={itemClickType}
            className={`close-btn ${size} ${className}`}
            style={style}
            onTouchEnd={(e) => {
                // todo: 关闭打点 trace.ts
                bridge?.close();
                ksbLog({
                    actionType: 3,
                });
                e.stopPropagation();
            }}
        >
            {text}
        </div>
    );
};

export default CloseButton;
